<template>
  <div class="container">
    <!--- <img src="../assets/grand.svg" width="50" /> -->
    <h1>Birthday Wall</h1>
    <!-- <button v-on:click="getCookie()">בדיקה</button> -->

    <h4>הפעל קונפטי</h4>
    <div class="confetti-wrapper">
      <div class="confetti-btn" id="play">
        <font-awesome-icon v-on:click="start" icon="play" />
      </div>
      <div class="confetti-btn" id="stop">
        <font-awesome-icon v-on:click="stop" icon="pause" />
      </div>
      <div class="confetti-btn" id="love">
        <font-awesome-icon v-on:click="love" icon="heart" />
      </div>
    </div>

    <div class="create-post" v-if="!cookie">
      <label for="text">כתוב ברכה:</label>
      <textarea name id="text" cols="30" rows="10" v-model="text"></textarea>
      <br />
      <label for="text">שם הכותב:</label>
      <div class="w100">
        <input type="author" id="author" v-model="author" />
      </div>
      <button :disabled="!text" v-on:click="createPost" class="postit">פרסם!</button>
    </div>
    <hr />
    <p class="error" v-if="error">{{ error }}</p>
    <div class="posts-container">
      <div
        class="post"
        v-for="(post, index) in posts"
        v-bind:item="post"
        v-bind:index="index"
        v-bind:key="post._id"
      >
        <div
          class="created-at"
        >{{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()+1}/${post.createdAt.getFullYear()}` }}</div>
        <p class="text">{{ post.text }}</p>
        <p class="author">{{post.author}}</p>
        <div class="oprations" v-if="identifyUser(post._id)">
          <font-awesome-icon
            id="delete"
            v-on:click="popupDelete=true; selectPost(post);"
            icon="trash"
          />
          <!-- <button id="delete" v-on:click="popupDelete=true; selectPost(post);">מחק</button> -->
          <!-- <button id="edit" v-on:click="popupEdit=true; selectPost(post);">ערוך</button> -->
          <font-awesome-icon id="edit" v-on:click="popupEdit=true; selectPost(post);" icon="pen" />
        </div>
      </div>
    </div>

    <div class="countdown-wrapper" dir="ltr">
      <p dir="rtl">יום הולדת עברי בעוד...</p>
      <countdown deadline="2019-12-7 00:00:00"></countdown>
    </div>
    <!---edit popup-->
    <div class="edit-modal" v-if="popupEdit">
      <!-- Modal content -->
      <div class="modal-content">
        <span class="close" v-on:click="popupEdit=false;">&times;</span>
        <h3>עריכה</h3>
        <hr />
        <textarea name id="pop-text" cols="30" rows="10" v-model="clickedPost.text"></textarea>
        <div class="w100">
          <input type="text" id="pop-author" v-model="clickedPost.author" />
        </div>
        <div class="pop-operations">
          <button class="save" v-on:click="popupEdit=false; updatePost();">שמור</button>
          <button class="cancle" v-on:click="popupEdit=false;">ביטול</button>
        </div>
      </div>
    </div>
    <!--End popup-->

    <!---Delete popup-->
    <div class="delete-modal" v-if="popupDelete">
      <!-- Modal content -->
      <div class="modal-content">
        <span class="close" v-on:click="popupDelete=false;">&times;</span>
        <h3>מחיקה</h3>
        <hr />
        <p>האם אתה בטוח?</p>
        <div class="pop-operations">
          <button class="cancle" v-on:click="popupDelete=false; deletePost(id);">מחק</button>
          <button class="save" v-on:click="popupDelete=false;">ביטול</button>
        </div>
      </div>
    </div>
    <!--End popup-->
  </div>
</template>

<script>
import Vue from "vue";
import PostService from "../PostService";
// import fontawesome from "@fortawesome/fontawesome-free";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faPlay,
  faPause,
  faHeart,
  faPen,
  faTrash
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import FlipCountdown from "vue2-flip-countdown";
import VueConfetti from "vue-confetti";
Vue.use(VueConfetti);

library.add(faPlay, faPause, faHeart, faPen, faTrash);

Vue.component("font-awesome-icon", FontAwesomeIcon);
Vue.component("countdown", FlipCountdown);

export default {
  name: "PostComponent",
  data() {
    return {
      posts: [],
      error: "",
      text: "",
      author: "",
      popupEdit: false,
      popupDelete: false,
      clickedPost: {},
      cookie: ""
    };
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.error = err.message;
    }
  },
  mounted() {
    this.identifyUser();
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text, this.author);
      this.posts = await PostService.getPosts();
    },
    async deletePost(id) {
      await PostService.deletePost(this.clickedPost._id);
      this.posts = await PostService.getPosts();
      this.cookie = "";
      this.text = "";
    },

    selectPost: function(post) {
      this.clickedPost = post;
    },
    async updatePost() {
      await PostService.editPost(
        this.clickedPost._id,
        this.clickedPost.text,
        this.clickedPost.author
      ).then(res => {
        this.clickedPost = {};

        if (res.data.error) {
          this.error = res.data.message;
        } else {
          // return console.log("here");
        }
      });
      this.posts = await PostService.getPosts();
    },
    getCookie: function() {
      if (document.cookie) {
        var coo = document.cookie;
        var array_cookie = coo.split("=");
        var cook = array_cookie[1].replace("j%3A%22", "");
        var cookie = cook.replace("%22", "");
        return cookie;
        // return console.log(cookie);
      } else {
        return undefined;
      }
      // this.cookie = cookie;
    },
    identifyUser(id) {
      if (this.getCookie() && id === this.getCookie()) {
        this.cookie = "yes";
        return true;
      } else {
        // this.cookie = "";
        return false;
      }
      // return this.getCookie() && id === this.getCookie() ? true : false;
    },
    start() {
      this.$confetti.start();
    },

    stop() {
      this.$confetti.stop();
    },

    love() {
      this.$confetti.update({
        particles: [
          {
            type: "heart"
          }
        ],
        defaultColors: ["red", "pink", "#ba0000"]
      });
    }
  }
};
</script>

